import React from 'react';
import { Container, Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';

export default class Help extends React.Component {
  render() {
    return (
      <Container>
        <h1>Bem-vindo(a) <span class="cap">{this.props.user.name}</span></h1>
        <p>
          Voc foi convidado para participar do processo de avaliao de um <strong>Sistema de Recomendao</strong> que visa 
          recomendar pesquisadores com potencial de colaborao na UNIPAMPA. A recomendao  feita com base na similaridade
           do contedo de projetos de pesquisa extrados da plataforma SIPPEE. Este,  um trabalho de concluso de curso do discente
           Wolgan Ens Quepfert do curso de Cincia da Computao sob orientao da docente Andra Sabedra Bordin. Nesta pgina encontram-se detalhes sobre a 
           utilizao do sistema e sobre o processo de avaliao.
        </p>
        <Row>
          <Col xs={6}>
            <h2>Navegao</h2>
            No menu superior de navegao as seguintes pginas esto disponveis:
            <ul>
              <li>
                <strong> Minhas Recomendaes: </strong>
                Nesta pgina, voc tem acesso a um grafo dos pesquisadores que foram recomendados para voc. Pesquisadores 
                com os quais voc j colaborou no passado aparecem com o rtulo "Coautor" sobre o vrtice que o representa. Abaixo de cada 
                vrtice h um valor percentual indicando a fora da recomendao.
              </li>
              <li>
                <strong>Grafo de Recomendaes: </strong>
                Nesta pgina, voc tem acesso a uma viso geral da rede de recomendaes feitas pelo sistema de recomendao 
                desenvolvido. Esta rede  representada por um grafo, onde os vrtices (crculos) representam pesquisadores e 
                as arestas (linhas) representam as recomendaes feitas. A espessura da linha da aresta representa a fora da 
                recomendao e linhas vermelhas representam relaes de coautoria, ou seja, indica que os pesquisadores envolvidos
                na relao, j colaboraram em algum trabalho anterior.
              </li>
            </ul>
          </Col>
          <Col xs={6}>
            <h2>Avaliao das Recomendaes</h2>
            Neste processo, convidamos voc a avaliar as recomendaes que o sistema realizou. Voc realizar uma avaliao 
            das recomendaes feitas do ponto de vista de usurio. Dessa forma, as recomendaes devem ser avaliadas de acordo com determinados
            aspectos. Os aspectos abordados neste objeto de avaliao so os apresentados em   
            <a href="https://dl.acm.org/citation.cfm?id=2043962" target="_blank"> Pu, Chen e Hu (2011)</a>, onde os autores 
            levantam pontos que devem ser levados em considerao quando se avalia um sistema de recomendao do ponto de vista do usurio: 
            <ul>
              <li>
                <strong>Preciso percebida: </strong> Os pesquisadores recomendados so de meu interesse.
              </li>
              <li>
                <strong>Novidade (ou descoberta): </strong> Descobri pesquisadores com potencial de colaborao que eu no conhecia anteriormente.
              </li>
              <li>
                <strong>Diversidade: </strong> Foram sugeridos diversos pesquisadores.
              </li>
              <li>
                <strong>Transparncia: </strong> Consegui perceber porque os pesquisadores foram recomendados para mim.
              </li>
              <li>
                <strong>Atratividade: </strong> Fiquei entusiasmado com as novas possibilidades de colaborao.
              </li>
              <li>
                <strong>Facilidade de uso: </strong> No encontrei dificuldades na utilizao do sistema.
              </li>
              <li>
                <strong>Utilidade percebida: </strong> A tarefa de encontrar colegas para colaborar em projetos ficou mais fcil de ser executada.
              </li>
              <li>
                <strong>Intenes do usurio: </strong> Caso implantado, utilizaria o sistema frequentemente.
              </li>
            </ul>
            <p>
              A fim de avaliar o sistema de recomendao e por consequncia, as recomendaes geradas, foi implantado um mecanismo de avaliao 
              da lista de potenciais colaboradores gerados e do grafo da rede de recomendao. Este mecanismo foi construdo no formato de
              um questionrio, onde os usurios do sistema (pesquisadores participantes da pesquisa), podem indicar a relevncia das recomendaes 
              feitas.  Desta forma, os resultados das entrevistas sero utilizados para o aperfeioamento contnuo do sistema.
            </p>
            <p>
              Para acessar o formulrio de avaliao, basta navegar at o link onde so exibidas as recomendaes feitas para voc. Ao acessar a pgina,
              no canto inferior esquerdo existe um boto chamado <strong>"Avaliar Recomendaes".</strong> Ao clicar nesse boto, voc ser redirecionado para 
              o formulrio onde para cada aspecto apresentado anteriormente, h uma questo equivalente. As respostas das questes so valores numricos em um intervalo
              de 1 (discordo completamente) at 5 (concordo completamente).
            </p>
          </Col>
        </Row>        
      </Container>
    )    
  }
}


